import { defineStore } from 'pinia'
import { MessageBox, Message } from 'element-ui'
import _ from 'lodash'

import {
  index as indexTemplate,
  show as showTemplate,
  store as storeTemplate,
  update as updateTemplate,
  destroy as deleteTemplate,
  getPage,
  updateContent
} from '@/api/template/index'

export const useStoreTemplate = defineStore('template', {
  state: () => ({
    isEdit: false,
    loading: false,
    dialogVisible: false,
    isFile: true,
    sizePerPage: 25,
    totalPages: '',
    totalItemPage: '',
    currentPage: '',
    contentHtml: '',
    filterDateRegisterRange: '',
    filterForm: {
      query: '',
      initDate: '',
      endDate: ''
    },
    keyName: '',
    typeTemplate: '',
    templateBlank: {
      title: '',
      keyName: '',
      type: '',
      content: '<p>Template</p>'
    },
    form: {},
    list: null,
    template: {},
    file: {
      name: '',
      pathFile: '',
      type: '',
      order: 0
    },
    isVisibleNavBar: true,
    dialogTitle: '',
    isJavascript: false,
    editor: {},
    pickerOptions: {
      shortcuts: [
        {
          text: 'Semana pasada',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', [start, end])
          }
        },
        {
          text: 'Mes pasado',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
            picker.$emit('pick', [start, end])
          }
        },
        {
          text: '3 Meses atrás',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
            picker.$emit('pick', [start, end])
          }
        },
        {
          text: '6 Meses atrás',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 180)
            picker.$emit('pick', [start, end])
          }
        },
        {
          text: '9 Meses atrás',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 270)
            picker.$emit('pick', [start, end])
          }
        },
        {
          text: '1 Año atrás',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 365)
            picker.$emit('pick', [start, end])
          }
        }
      ]
    }
  }),
  actions: {
    // Template
    async indexTemplate (filterForm, type) {
      this.loading = true
      return await indexTemplate(filterForm, type)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }

          this.list = response.data.data
          this.totalPages = response.data.total

          return this.list
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (this.loading = false))
    },
    async showTemplate (keyName) {
      this.loading = true
      return await showTemplate(keyName)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })

          this.form = response.data

          return this.form
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (this.loading = false))
    },
    async storeTemplate (dataTemplate) {
      this.loading = true
      return await storeTemplate(dataTemplate)
        .then((response) => {
          this.template = response.data
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })

          return this.template
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (this.loading = false))
    },
    async updateTemplate (keyName, dataTemplate) {
      this.loading = true
      return await updateTemplate(keyName, dataTemplate)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })

          this.form = response.data
          return this.form
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (this.loading = false))
    },
    async deleteTemplate (keyName) {
      this.loading = true
      return await deleteTemplate(keyName)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })

          this.list = response.data

          return this.list
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (this.loading = false))
    },
    async updateContentHtml (dataEditor, keyName) {
      // this.loading = true
      return await updateContent({ content: dataEditor }, keyName)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })

          return response.data
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
      // .finally(() => (this.loading = false))
    },

    // Dialog
    handleCreate (index, row) {
      this.isEdit = false
      this.dialogVisible = true
      this.form = _.cloneDeep(this.templateBlank)
    },
    handleClose (done) {
      MessageBox.confirm('Estas deacuerdo en cerrar la ventana?')
        .then((_) => {
          done()
        })
        .catch((_) => {})
    },

    // filters

    async handleCurrentChange (page) {
      this.currentPage = page
      this.loading = true
      const type = _.cloneDeep(this.typeTemplate)
      await getPage(type, page, this.sizePerPage)
        .then((response) => {
          this.list = response.data.data
          this.totalPages = response.data.total
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },
    async handleChangeItemPage (val) {
      this.loading = true
      this.sizePerPage = val
      const type = _.cloneDeep(this.typeTemplate)
      await getPage(type, this.currentPage, val)
        .then((response) => {
          this.list = response.data.data
          this.totalPages = response.data.total
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },
    handleFilterDateRegister () {
      if (this.filterDateRegisterRange) {
        this.filterForm.initDate = this.filterDateRegisterRange[0]
        this.filterForm.endDate = this.filterDateRegisterRange[1]
        this.handleSearchFilter()
      } else {
        this.filterForm.initDate = ''
        this.filterForm.endDate = ''
        this.handleSearchFilter()
      }
    },
    handleClean () {
      this.filterForm.query = ''
      this.filterForm.initDate = ''
      this.filterForm.endDate = ''
      this.handleSearchFilter()
    },
    async handleSearchFilter () {
      this.loading = true
      await this.indexTemplate(this.filterForm, this.typeTemplate).finally(
        () => (this.loading = false)
      )
    },
    showNavBar () {
      if (this.isVisibleNavBar) {
        this.isVisibleNavBar = false
      } else {
        this.isVisibleNavBar = true
      }
    }
  }
})
