<template>
  <el-row
    :gutter="10"
    type="flex"
    justify="center"
    v-loading="templateStore.loading"
    element-loading-text="Cargando..."
  >
    <el-col :xs="22" :sm="22" :md="19" :lg="19" :xl="19">
      <div>
        <ValidationObserver ref="form" v-slot="{ invalid }">
          <el-form
            label-position="top"
            label-width="100px"
            :model="templateStore.form"
          >
            <el-form-item label="Titulo">
              <ValidationProvider
                name="titulo"
                rules="required"
                v-slot="{ errors }"
              >
                <el-input
                  size="mini"
                  v-model="templateStore.form.title"
                ></el-input>
                <span>{{ errors[0] }}</span>
              </ValidationProvider>
            </el-form-item>

            <el-form-item label="KeyName">
              <div v-if="templateStore.isEdit">
                <ValidationProvider
                  name="keyName"
                  rules="required"
                  v-slot="{ errors }"
                >
                  <el-input
                    size="mini"
                    v-model="templateStore.form.keyName"
                    :disabled=true
                  ></el-input>
                  <span>{{ errors[0] }}</span>
                </ValidationProvider>
              </div>
              <div v-else>
                <ValidationProvider
                  name="keyName"
                  rules="uniqueKeyNameTemplate|required"
                  v-slot="{ errors }"
                >
                  <el-input
                    size="mini"
                    v-model="templateStore.form.keyName"
                  ></el-input>
                  <span>{{ errors[0] }}</span>
                </ValidationProvider>
              </div>
            </el-form-item>

            <!-- Butons action -->
            <el-form-item>
              <div align="right">
                <el-button
                  @click.stop.prevent="handleCancel"
                  type="danger"
                  size="medium"
                >
                  Cancelar</el-button
                >
                <el-button
                  @click.stop.prevent="checkSave"
                  :type="templateStore.isEdit ? 'primary' : 'success'"
                  size="medium"
                  :disabled="invalid"
                >
                  {{ templateStore.isEdit ? 'Actualizar' : 'Crear' }}</el-button
                >
              </div>
            </el-form-item>
            <!-- End button -->
          </el-form>
        </ValidationObserver>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import { useStoreTemplate } from '../../../stores/template/templateStore'
import { useStoreConfig } from '../../../stores/configStore'
import _ from 'lodash'

const configStore = useStoreConfig()
const templateStore = useStoreTemplate()

export default {
  name: 'Settings',
  data () {
    return {
      isUpdateForm: false,
      valueLang: '',
      configStore,
      templateStore
    }
  },
  async created () {},
  methods: {
    async checkSave () {
      if (templateStore.isEdit) {
        const settingData = _.cloneDeep(templateStore.form)
        const keyName = _.cloneDeep(templateStore.keyName)
        templateStore.updateTemplate(keyName, settingData)
      } else {
        templateStore.form.type = templateStore.typeTemplate
        const settingData = _.cloneDeep(templateStore.form)
        await templateStore.storeTemplate(settingData).then((response) => {
          const keyName = _.cloneDeep(response.keyName)
          if (templateStore.typeTemplate === 'main') {
            this.$router.push({
              name: 'main.edit.content',
              params: { keyName: keyName }
            })
          } else if (templateStore.typeTemplate === 'header') {
            this.$router.push({
              name: 'header.edit.content',
              params: { keyName: keyName }
            })
          } else if (templateStore.typeTemplate === 'footer') {
            this.$router.push({
              name: 'footer.edit.content',
              params: { keyName: keyName }
            })
          }
          templateStore.dialogVisible = false
        })
      }
    },
    handleCancel () {
      if (templateStore.dialogVisible) {
        templateStore.dialogVisible = false
      } else {
        this.returnToIndex()
      }
    },
    returnToIndex () {
      if (templateStore.typeTemplate === 'main') {
        this.$router.push({ name: 'main.index' })
      } else if (templateStore.typeTemplate === 'header') {
        this.$router.push({ name: 'header.index' })
      } else if (templateStore.typeTemplate === 'footer') {
        this.$router.push({ name: 'footer.index' })
      }
    }
  }
}
</script>
<style scoped>
.text-color-gray {
  color: #606266;
}
</style>
