<template>
  <div class="app-container">
    <el-row class="margin-bottom-20">
      <el-col :lg="16" :xs="16" :offset="0">
        <el-input
          prefix-icon="el-icon-search"
          size="mini"
          placeholder="Nombre, mensaje..."
          v-model="templateStore.filterForm.query"
          v-on:change="handleSearchFilter()"
          clearable
        ></el-input>
      </el-col>
      <el-col :lg="2" :xs="8" :offset="0">
        <el-button
          @click.stop.prevent="templateStore.handleSearchFilter"
          class="margin-left-10"
          size="mini"
          type="primary"
          icon="el-icon-search"
          >Buscar</el-button
        >
      </el-col>
      <el-col :lg="2" :xs="8" :offset="0">
        <el-button
          @click.stop.prevent="templateStore.handleClean"
          size="mini"
          class="margin-left-10"
          type="warning"
          icon="el-icon-delete"
          >Limpiar</el-button
        >
      </el-col>
      <el-col :lg="2" :xs="8" :offset="0">
        <el-button
          @click.stop.prevent="templateStore.handleCreate"
          size="mini"
          class="margin-left-10"
          type="success"
          icon="el-icon-plus"
          >Nuevo</el-button
        >
      </el-col>
    </el-row>

    <template>
      <el-col :lg="6" :xs="24" :offset="0">
        <span class="margin-filter">Registros por fecha</span>
        <el-date-picker
          v-model="templateStore.filterDateRegisterRange"
          v-on:change="handleFilterDateRegister()"
          size="mini"
          type="daterange"
          align="right"
          unlink-panels
          format="dd-MM-yyyy"
          range-separator="a"
          start-placeholder="Fecha inicial"
          end-placeholder="Fecha final"
          :picker-options="templateStore.pickerOptions"
        >
        </el-date-picker>
      </el-col>
    </template>

    <el-row type="flex" justify="end">
      <el-pagination
        @size-change="templateStore.handleChangeItemPage"
        :page-sizes="[25, 50, 100, 200]"
        :page-size="25"
        layout="total,sizes,prev, pager, next"
        @current-change="templateStore.handleCurrentChange"
        :total="parseInt(templateStore.totalPages)"
      >
      </el-pagination>
    </el-row>

    <!--Start Dialog-->
    <template>
      <el-dialog
        title="Crear Plantilla Footer"
        :visible.sync="templateStore.dialogVisible"
        width="30%"
        style="padding: 30px"
        :before-close="templateStore.handleClose"
      >
        <settings />
      </el-dialog>
    </template>
    <!-- End Dialog -->

    <el-table
      size="mini"
      v-loading="templateStore.loading"
      :data="templateStore.list"
      element-loading-text="Cargando..."
      fit
      highlight-current-row
      style="width: 100%"
    >
      <el-table-column label="Nombre" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.title }}</span>
        </template>
      </el-table-column>
      <el-table-column label="Registro" align="center">
        <template slot-scope="scope">
          {{ scope.row.created_at | formatDate }}
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="Opciones" align="center">
        <template slot-scope="scope">
          <el-button size="mini" @click="configEdit(scope.$index, scope.row)"
            >Editar configuracion</el-button
          >
          <el-button size="mini" @click="contentEdit(scope.$index, scope.row)"
            >Editar contenido</el-button
          >
          <el-button
            size="mini"
            type="danger"
            :loading="scope.row.loadingDelete"
            @click.native.prevent="handleDelete(scope.$index, scope.row)"
            >Eliminar</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <el-row type="flex" justify="end">
      <el-pagination
        @size-change="templateStore.handleChangeItemPage"
        :page-sizes="[25, 50, 100, 200]"
        :page-size="25"
        layout="total,sizes,prev, pager, next"
        @current-change="templateStore.handleCurrentChange"
        :total="parseInt(templateStore.totalPages)"
      >
      </el-pagination>
    </el-row>
  </div>
</template>

<script>
// import { searchF, getPage } from '@/api/template/footer'
import { useStoreTemplate } from '../../../stores/template/templateStore'
import settings from '../components/settings.vue'
import _ from 'lodash'

const templateStore = useStoreTemplate()

export default {
  components: {
    settings
  },
  data () {
    return {
      templateStore
    }
  },
  created () {
    templateStore.typeTemplate = 'footer'
    const filterForm = _.cloneDeep(templateStore.filterForm)
    const type = _.cloneDeep(templateStore.typeTemplate)
    templateStore.indexTemplate(filterForm, type)
  },
  mounted () {
  },
  methods: {
    configEdit (index, row) {
      this.$router.push({
        name: 'footer.edit.config',
        params: { keyName: row.keyName, typeTemplate: 'footer' }
      })
    },
    contentEdit (index, row) {
      this.$router.push({
        name: 'footer.edit.content',
        params: { keyName: row.keyName }
      })
    },
    handleCreate (index, row) {
      templateStore.dialogVisible = true
      // this.$router.push({ name: 'footer.create' })
    },
    handleDelete (index, row) {
      const keyName = _.cloneDeep(row.keyName)
      this.$confirm('Se borrara la plantilla, ¿Esta seguro?', 'Cuidado', {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancelar',
        type: 'warning'
      })
        .then(() => {
          templateStore.deleteTemplate(keyName).then((response) => {
            if (response) {
              templateStore.list.splice(index, 1)
            }
          })
        })
        .catch(() => {
          this.$message({
            showClose: true,
            type: 'info',
            message: 'Se cancelo la operación'
          })
        })
    }
  }
}
</script>
<style lang="scss" scoped></style>
